/* ==========================================================================
   #Content bottom styles - begin
   ========================================================================== */

.mkdf-content-bottom {
    
    .mkdf-content-bottom-inner{
        box-sizing: border-box;
        
        &.mkdf-grid{
            padding: 20px 0;
        }
        
        &.mkdf-full-width{
            padding: 20px 50px;
        }
    }
}
/* ==========================================================================
   #Content bottom styles - end
   ========================================================================== */

/* ==========================================================================
   Footer style - begin
   ========================================================================== */

.mkdf-page-footer {
    @include mkdfRelativeHolderLayout();
    z-index: 100;
    box-sizing: border-box;

    .mkdf-footer-top-holder {
        position: relative;
        display: block;
        width: 100%;
        vertical-align: middle;
        background-color: #252b33;
        box-sizing: border-box;

        .mkdf-footer-top-alignment-center{
            text-align: center;
        }
	    
        .mkdf-footer-top-alignment-left{
            text-align: left;
        }
	    
        .mkdf-footer-top-alignment-right{
            text-align: right;
        }

        .mkdf-footer-top-inner{
            box-sizing: border-box;
	        
            &.mkdf-grid{
                padding: 103px 0 41px;
            }
	        
            &.mkdf-full-width{
                padding: 76px 50px;
            }
        }
    }

    .mkdf-footer-bottom-holder {
        @include mkdfRelativeHolderLayout();
        background-color: #252b33;
        box-sizing: border-box;
        text-align: center;

        .mkdf-footer-bottom-inner {
            box-sizing: border-box;
	        
            &.mkdf-grid {
                padding: 19px 0;
            }
	        
            &.mkdf-full-width {
                padding: 19px 50px;
            }
        }
    }

    .mkdf-listing-widget {
        .mkdf-ls-list-holder .mkdf-ls-list-items-holder {
            &.mkdf-ls-list-simple {
                .mkdf-ls-adr-pin {
                    a {
                        color: #fff;

                        &:hover {
                            color: $first-main-color;

                        }

                        i {
                            color: #fff;

                            &:hover {
                                color: $first-main-color;
                            }
                        }
                    }
                }

                .mkdf-ls-adr-city {
                    .mkdf-city {
                        color: #fff;

                        &:hover {
                            color: $first-main-color;
                        }
                    }
                }
            }
        }
    }
}

// dark skin

footer {

    &.mkdf-page-footer.mkdf-footer-dark-skin {

        .widget {
            color: $default-text-color;

            .mkdf-widget-title-holder .mkdf-widget-title {
                color: #252b33;
            }

            &.widget_mkdf_twitter_widget .mkdf-twitter-widget.mkdf-twitter-standard li {


                .mkdf-tweet-text {
                    color: $default-text-color;

                    a,
                    span {
                        color: $default-text-color;

                        &:hover {
                            color: $first-main-color;
                        }
                    }
                }
            }

            a {
                color: $default-text-color;

                &:hover {
                    color: $first-main-color;
                }
            }

            p {
                color: $default-text-color;
            }

            &.widget_pages,
            &.widget_archive,
            &.widget_categories,
            &.widget_meta,
            &.widget_recent_entries,
            &.widget_nav_menu {

                ul {

                    li {

                        a {
                            color: #8a959e;

                            &:after {
                                color: #97a1a9;
                            }

                            &:hover {
                                color: $first-main-color;

                                &:after {
                                    color: $first-main-color;
                                }
                            }
                        }
                    }
                }
            }

            &.mkdf-listing-widget {
                .mkdf-ls-list-simple {
                    .mkdf-listing-title {
                        a {
                            color: $default-heading-color;

                            &:hover {
                                color: $first-main-color;
                            }
                        }
                    }

                    .mkdf-ls-adr-pin {
                        a {
                            color: $additional-text-color;

                            &:hover {
                                color: $first-main-color;
                            }

                            i {
                                color: $additional-text-color;

                                &:hover {
                                    color: $first-main-color;
                                }
                            }
                        }
                    }

                    .mkdf-ls-adr-city {
                        .mkdf-city {
                            color: $additional-text-color;

                            &:hover {
                                color: $first-main-color;
                            }
                        }
                    }
                }
            }

        }

        a {
            color: #97a1a9;

            &:hover {
                color: $first-main-color;
            }
        }

        .mkdf-footer-bottom-holder {
            p {
                color: #8a959e;
            }
        }
    }
}

/* ==========================================================================
   Footer style - end
   ========================================================================== */